export function getMq() {
    var windowWidth = $(window).outerWidth()
        , mq = 0
        , string = '';
    // check if you can get external document width
    if (window.parent && window.parent.document && window.parent.document.body && window.parent.document.body.clientWidth) {
        windowWidth = window.parent.document.body.clientWidth;
    }
    switch (true) {
        case windowWidth >= 1440:
            mq = 5;
            string = 'Visible mq: > 1440px';
            break;
        case windowWidth >= 1280:
            mq = 4;
            string = 'Visible mq: > 1280px';
            break;
        case windowWidth >= 1024:
            mq = 3;
            string = 'Visible mq: > 1024px';
            break;
        case windowWidth >= 600:
            mq = 2;
            string = 'Visible mq: > 600px';
            break;
        case windowWidth >= 480:
            mq = 1;
            string = 'Visible mq: > 480px';
            break;
        default:
            mq = 0;
            string = 'None';
    }
    $('.js-width').text(string);
    return mq;
}
;

export function getScreenSize() {
    if (window.parent && window.parent.document && window.parent.document.body && window.parent.document.body.clientWidth) {
        return [window.parent.document.body.clientWidth, $(window).outerHeight()];
    } else {
        return [$(window).outerWidth(), $(window).outerHeight()];
    }
}
;

export function getValidResponsiveValue(spaceArray, index, subIndex) {
    var item = (subIndex !== false) ? spaceArray[index][subIndex] : spaceArray[index];
    if (index == 0)
        return item;
    if (item !== false && item !== '') {
        return item;
    } else {
        return getValidResponsiveValue(spaceArray, index - 1, subIndex);
    }
}
